<template>
  <v-content>
    <Cabecera />
    <v-container>
      <v-row>
        <v-col cols="12" sm="4">
          <div class="perfil-foto elevation-3">
            <v-img src="@/assets/avatar/normal.png" aspect-ratio="1" contain></v-img>
          </div>
        </v-col>
        <v-col cols="12" sm="8">
          <div v-if="loading_perfil" class="loading-perfil text-center">
            <h3 class="mb-5">Cargando Perfil</h3>
            <v-progress-circular :size="50" color="#1976d2" indeterminate></v-progress-circular>
          </div>
          <div v-else class="perfil-datos elevation-3">
            <table style="width: 100%">
              <tbody>
                <tr>
                  <td>Nombre:</td>
                  <td>
                    <input type="text" v-model="perfil.nombre" :readonly="!editar" />
                  </td>
                </tr>
                <tr>
                  <td>Apellido Paterno:</td>
                  <td>
                    <input type="text" v-model="perfil.apellido_paterno" :readonly="!editar" />
                  </td>
                </tr>
                <tr>
                  <td>Apellido Materno:</td>
                  <td>
                    <input type="text" v-model="perfil.apellido_materno" :readonly="!editar" />
                  </td>
                </tr>
              </tbody>
            </table>
            <div class="d-flex justify-end mt-5">
              <v-btn color="primary" v-if="!editar" @click="editar = true">
                Editar
                <v-icon right dark class="ml-3 mr-1">mdi-pencil</v-icon>
              </v-btn>
              <v-btn :loading="guardando" color="primary" @click="editarPerfil" v-if="editar">
                Guardar
                <v-icon right dark class="ml-3 mr-1">mdi-content-save</v-icon>
              </v-btn>
              <v-btn class="ml-3" v-if="editar" @click="editar = false">Cancelar</v-btn>
            </div>
          </div>
        </v-col>
      </v-row>
      <v-card v-if="sesion && sesion['tipo'] == 'alumno'" class="contenedor-estilo my-2">
        <span class="my-4 titulo-estilo">Estilo de Aprendizaje</span>
        <canvas id="myChart" width="600" height="400"></canvas>
        <div class="d-flex justify-end my-5">
          <v-btn color="primary" @click="ingresarTest()">
            Nuevo Test
            <v-icon right dark class="ml-3 mr-1">mdi-ballot-outline</v-icon>
          </v-btn>
        </div>
      </v-card>
    </v-container>
  </v-content>
</template>

<script>
import Chart from "chart.js";
import Cabecera from "../components/Cabecera";
import Perfil from "../models/Perfil";

export default {
  data() {
    return {
      myChart: null,
      sesion: null,
      perfil: null,
      editar: false,
      guardando: false,
      loading_perfil: true,
      // Estilo
      comprension: "",
      comprension_valor: 0,
      entrada: "",
      entrada_valor: 0,
      percepcion: "",
      percepcion_valor: 0,
      procesamiento: "",
      procesamiento_valor: 0
    };
  },
  mounted() {
    this.sesion = this.$store.state.servicio.sesion.obtenerSesion();
    this.obtenerPerfil();
    if (this.sesion["tipo"] == "alumno") {
      this.obtenerEstiloAprendizaje();
    }
  },
  methods: {
    obtenerPerfil() {
      let obtener =
        this.sesion.tipo == "profesor"
          ? this.$store.state.servicio.profesor.obtenerProfesorPorId
          : this.$store.state.servicio.alumno.obtenerAlumnoPorId;

      obtener(
        this.sesion["id"],
        res => {
          this.loading_perfil = false;
          this.perfil = new Perfil(
            res["profesor_id"] || res["alumno_id"],
            res["codigo"],
            res["nombre"],
            res["apellido_paterno"],
            res["apellido_materno"]
          );
        },
        err => {
          this.loading_perfil = false;
          console.log(err);
        }
      );
    },
    verDashboard() {
      if (this.myChart) this.myChart.destroy();
      var ctx = document.getElementById("myChart").getContext("2d");
      this.myChart = new Chart(ctx, {
        type: "bar",
        data: {
          labels: [
            this.procesamiento,
            this.percepcion,
            this.entrada,
            this.comprension
          ],
          datasets: [
            {
              label: ["Estilo de Aprendizaje"],
              data: [
                this.procesamiento_valor,
                this.percepcion_valor,
                this.entrada_valor,
                this.comprension_valor
              ],
              backgroundColor: [
                "rgba(255, 99, 132, 0.2)",
                "rgba(54, 162, 235, 0.2)",
                "rgba(255, 206, 86, 0.2)",
                "rgba(75, 192, 192, 0.2)",
                "rgba(153, 102, 255, 0.2)",
                "rgba(255, 159, 64, 0.2)"
              ],
              borderColor: [
                "rgba(255, 99, 132, 1)",
                "rgba(54, 162, 235, 1)",
                "rgba(255, 206, 86, 1)",
                "rgba(75, 192, 192, 1)",
                "rgba(153, 102, 255, 1)",
                "rgba(255, 159, 64, 1)"
              ],
              borderWidth: 1
            }
          ]
        },
        options: {
          legend: {
            display: false
          },
          scales: {
            yAxes: [
              {
                ticks: {
                  beginAtZero: true,
                  max: 11,
                  stepSize: 1
                }
              }
            ]
          }
        }
      });
    },
    editarPerfil() {
      let actualizar =
        this.sesion.tipo == "profesor"
          ? this.$store.state.servicio.profesor.actualizarProfesor
          : this.$store.state.servicio.alumno.actualizarAlumno;

      this.guardando = true;
      actualizar(
        this.perfil.id,
        this.perfil.nombre,
        this.perfil.apellido_paterno,
        this.perfil.apellido_materno,
        res => {
          this.guardando = false;
          this.editar = false;
        },
        err => {
          this.guardando = false;
          console.log(err);
        }
      );
    },
    obtenerEstiloAprendizaje() {
      this.$store.state.servicio.estilo.obtenerEstiloAprendizajePorAlumnoId(
        this.sesion["id"],
        res => {
          if (res) {
            (this.comprension = res["comprension"]),
              (this.comprension_valor = res["comprension_valor"]),
              (this.entrada = res["entrada"]),
              (this.entrada_valor = res["entrada_valor"]),
              (this.percepcion = res["percepcion"]),
              (this.percepcion_valor = res["percepcion_valor"]),
              (this.procesamiento = res["procesamiento"]),
              (this.procesamiento_valor = res["procesamiento_valor"]);
            this.verDashboard();
          }
        },
        err => {
          console.log(err);
        }
      );
    },
    ingresarTest() {
      this.$router.push({ name: "test" });
    }
  },
  components: {
    Cabecera
  }
};
</script>

<style lang="scss" scoped>
.perfil-foto {
  background: #f9ceff;
}
.perfil-datos {
  padding: 20px 40px;
  background: #fff;
  border-top: 6px solid #7694f4;
  border-radius: 6px 6px 0 0;
}
.contenedor-estilo {
  padding: 2px 30px;
  margin: 0 auto;
  max-width: 600px;
}
.loading-perfil {
  padding: 40px;
}
.titulo-estilo {
  display: block;
  text-align: center;
  font-size: 20px;
}

/* */
table {
  border-collapse: separate;
  border-spacing: 0 1em;

  input[type="text"] {
    border: 1px solid #acacac;
    padding: 8px 16px;
    margin-left: 10px;
    width: 100%;
    border-radius: 10px;
    background: #fff;
  }
  input[type="text"]:read-only {
    color: #858585;
    border: 1px solid #ccc;
  }
}
</style>